































import ut from '../../utils/index.js';
export default {
	data() {
		return {
			check:-1,
			server: [],
			homeType:[],
			static:'',
			serverIndex:'-1'
		}
	},
	onLoad() {
		this.static=ut.static
		this.req_homeType();
		this.req_server();
	},
	methods: {
		cg_serverIndex(index){
			this.serverIndex=index
		},
		go_mine_infchange() {
			wx.navigateTo({
				url: '../mine/infchange'
			})
		},
		go_mine_apply() {
			wx.navigateTo({
				url: '../mine/apply'
			})
		},
		cg_check(index){
			if(this.check==index){
				this.check=-1;
				return;
			}
			this.check=index
		},
		req_homeType(){
			ut.request({
				url: "reform/houseTypeList",
				method:'get'
			}).then(data=>{
				this.homeType=data;
			})
		},
		req_server(){
			ut.request({
				url: "reform/reformTypeList",
				method:'get'
			}).then(data=>{
				this.server=data;
			})
		},
		go_home_remake(){
			if(this.serverIndex=='-1'){
				ut.totast('请选择改造类型');
				return;
			}
			if(this.check=='-1'){
				ut.totast('请选择户型');
				return;
			}
			wx.setStorageSync('remakeinf',{
				name:this.server[this.serverIndex].name,
				reformTypeId:this.server[this.serverIndex].id,
				houseTypeId:this.homeType[this.check].id,
				picture:ut.static+this.server[this.serverIndex].picture
			})
			if(wx.getStorageSync('token')){
				wx.navigateTo({
					url: '../home/remake'
				})
			}else{
				wx.navigateTo({
					url: '../mine/login'
				})
			}
		}
	}
}
